// components/dynamic-zone/services/index.tsx
'use client';

import React from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { motion, useReducedMotion } from 'framer-motion';
import { Calendar, Gem, Scissors, Sparkles, User } from 'lucide-react';

import { Heading } from '@/components/elements/heading';
import { Subheading } from '@/components/elements/subheading';
import { Button } from '@/components/elements/button';

/** IMPORTANT: ne defaultolj localhostra prod miatt */
const STRAPI_BASE = (
  process.env.NEXT_PUBLIC_STRAPI_URL ??
  process.env.NEXT_PUBLIC_API_URL ??
  ''
)
  .replace(/\/$/, '')
  .replace(/\/api$/, ''); // ha valaha /api-t adsz meg, ne legyen /api/uploads

type StrapiMedia = {
  data?: {
    attributes?: {
      url?: string;
      alternativeText?: string | null;
      width?: number | null;
      height?: number | null;
    } | null;
  } | null;

  url?: string;
  alternativeText?: string | null;
  attributes?: {
    url?: string;
    alternativeText?: string | null;
    width?: number | null;
    height?: number | null;
  } | null;
};

type DZItem = {
  __component: string;
  id: number;
  heading?: string | null;
  sub_heading?: string | null;

  // ✅ Hero mező
  cover?: StrapiMedia | null;
};

type PageLike = {
  id: number;
  documentId?: string;
  slug: string;
  label?: string | null;
  dynamic_zone?: DZItem[];
  order?: number;
  position?: number;
  sort?: number;
};

type ButtonTarget = '_self' | '_blank' | '_parent' | '_top' | string;

type ButtonItem = {
  id?: number;
  documentId?: string;
  text?: string | null;
  URL?: string | null;
  href?: string | null;
  variant?: string | null;
  target?: ButtonTarget | null;
  newTab?: boolean | null;
};

type CTAComponent = {
  id?: number;
  documentId?: string;
  heading?: string | null;
  sub_heading?: string | null;
  CTAs?: ButtonItem[] | null; // belső lista
};

function getHero(p: PageLike) {
  return p.dynamic_zone?.find((x) => x.__component === 'dynamic-zone.hero');
}

function getPageTitle(p: PageLike) {
  const label = p.label?.trim();
  if (label) return label;

  const hero = getHero(p);
  const t = hero?.heading?.trim();
  return t && t.length > 0 ? t : p.slug;
}

/** ✅ Placeholder helyett: Hero sub_heading */
function getPageSubheading(p: PageLike): string | null {
  const hero = getHero(p);
  const s = hero?.sub_heading?.trim();
  return s && s.length > 0 ? s : null;
}

function getMediaUrl(m?: StrapiMedia | null) {
  const url = m?.data?.attributes?.url ?? m?.attributes?.url ?? m?.url ?? undefined;
  if (!url) return null;

  if (/^https?:\/\//i.test(url)) return url;
  return STRAPI_BASE ? `${STRAPI_BASE}${url.startsWith('/') ? '' : '/'}${url}` : url;
}

function getMediaAlt(m?: StrapiMedia | null, fallback = '') {
  return (
    m?.data?.attributes?.alternativeText ??
    m?.attributes?.alternativeText ??
    m?.alternativeText ??
    fallback
  ) || fallback;
}

function getPageCover(p: PageLike) {
  const hero: any = getHero(p);
  const cover: StrapiMedia | null | undefined = hero?.cover ?? null;
  const src = getMediaUrl(cover);
  if (!src) return null;
  return { src, alt: getMediaAlt(cover, getPageTitle(p)) };
}

function toHref(btn: ButtonItem) {
  return (btn.URL ?? btn.href ?? '#') as string;
}

function toTarget(btn: ButtonItem) {
  const target = btn.newTab ? '_blank' : (btn.target ?? undefined);
  const rel = target === '_blank' ? 'noopener noreferrer' : undefined;
  return { target, rel };
}

const MotionDiv = motion.div;

export const Services = ({
  heading,
  sub_heading,
  pages,
  CTAs, // Strapi-ban ez egy komponens (CTA), benne CTAs[] gombokkal
}: {
  heading?: string | null;
  sub_heading?: string | null;
  pages?: PageLike[] | null;
  CTAs?: CTAComponent | null;
}) => {
  const reduce = useReducedMotion();
  const dur = reduce ? 0.01 : 0.45;
  const ease: any = [0.22, 1, 0.36, 1];

  // ✅ pages normalizálás + (ha van) order szerinti rendezés
  const pageItems: PageLike[] = (() => {
    const raw: any = pages as any;
    const list: any[] = Array.isArray(raw) ? raw : (raw?.data ?? []);
    if (!Array.isArray(list) || list.length === 0) return [];

    const getOrder = (p: any): number | null => {
      const v =
        p?.order ??
        p?.position ??
        p?.sort ??
        p?.__order ??
        p?.__pivot?.order ??
        p?.__pivot?.position ??
        p?.pivot?.order ??
        p?.pivot?.position ??
        p?.meta?.order ??
        p?.attributes?.order ??
        p?.attributes?.position;

      return typeof v === 'number' ? v : null;
    };

    const hasOrder = list.some((p) => getOrder(p) !== null);
    return hasOrder
      ? [...(list as PageLike[])].sort((a, b) => (getOrder(a) ?? 1e9) - (getOrder(b) ?? 1e9))
      : (list as PageLike[]);
  })();

  const featured = pageItems[0];
  const rightTop = pageItems[1];
  const rightBottom = pageItems[2];
  const bottomLeft = pageItems[3];
  const bottomMid = pageItems[4];

  // ✅ CTA kártya – csak ha van értelmes tartalom
  const primaryCTA = CTAs?.CTAs?.[0] ?? null;
  const ctaHref = primaryCTA ? toHref(primaryCTA) : null;
  const { target: ctaTarget, rel: ctaRel } = primaryCTA
    ? toTarget(primaryCTA)
    : { target: undefined, rel: undefined };

  const ctaTitle = (CTAs?.heading ?? '').trim();
  const ctaDesc = (CTAs?.sub_heading ?? '').trim();
  const ctaBtnText = (primaryCTA?.text ?? '').trim();

  const hasCtaCard = Boolean(
    ctaTitle ||
      ctaDesc ||
      (primaryCTA && ctaHref && ctaHref !== '#' && ctaBtnText)
  );

  const inViewProps = (delay = 0) => ({
    initial: { opacity: 0, y: reduce ? 0 : 12 },
    whileInView: { opacity: 1, y: 0 },
    viewport: { once: true, amount: 0.2 },
    transition: { duration: dur, ease, delay },
  });

  const hoverLift = reduce ? undefined : { y: -2 };

  const IconBadge = ({
    icon: Icon,
    variant = 'light',
  }: {
    icon: React.ComponentType<{ className?: string }>;
    variant?: 'light' | 'dark';
  }) => {
    const base =
      variant === 'dark'
        ? 'bg-black/25 backdrop-blur-md ring-1 ring-white/15'
        : 'bg-charcoal/60 ring-1 ring-secondary/10';
    return (
      <div className={`inline-flex h-12 w-12 items-center justify-center rounded-xl ${base}`}>
        <Icon className="h-5 w-5 text-muted" />
      </div>
    );
  };

  const WhiteCard = ({
    page,
    icon,
    delay,
  }: {
    page: PageLike;
    icon: React.ComponentType<{ className?: string }>;
    delay: number;
  }) => {
    const cover = getPageCover(page);
    const desc = getPageSubheading(page);

    return (
      <MotionDiv {...inViewProps(delay)} whileHover={hoverLift} className="h-full">
        <Link
          href={`/${page.slug}`}
          className={[
            'group relative flex h-full flex-col overflow-hidden rounded-xl bg-white p-8',
            'ring-1 ring-secondary/10 shadow-sm transition-shadow',
            'hover:shadow-md',
          ].join(' ')}
        >
          {cover ? (
            <>
              <div className="absolute inset-0">
                <Image
                  src={cover.src}
                  alt={cover.alt}
                  fill
                  sizes="(max-width: 1024px) 100vw, 33vw"
                  className="object-cover opacity-20"
                />
              </div>
              <div className="absolute inset-0 bg-white/85" />
            </>
          ) : null}

          <div className="relative z-10">
            <IconBadge icon={icon} variant="light" />

            <div className="mt-6">
              <div className="text-[18px] font-semibold tracking-tight text-black">
                {getPageTitle(page)}
              </div>

              {desc ? (
                <p className="mt-2 text-[13px] leading-relaxed text-secondary/65">{desc}</p>
              ) : null}
            </div>
          </div>

          <div className="relative z-10 mt-auto pt-6">
            <span className="inline-flex items-center gap-2 text-[12px] font-medium text-black/80">
              <span className="transition-colors group-hover:text-black">Fedezze fel</span>
              <span aria-hidden className="text-muted transition-transform group-hover:translate-x-1">
                →
              </span>
            </span>
          </div>
        </Link>
      </MotionDiv>
    );
  };

  const FeaturedCard = ({ page, delay }: { page: PageLike; delay: number }) => {
    const cover = getPageCover(page);
    const desc = getPageSubheading(page);

    return (
      <MotionDiv {...inViewProps(delay)} whileHover={hoverLift} className="h-full">
        <Link
          href={`/${page.slug}`}
          className={[
            'group relative block h-full overflow-hidden rounded-xl',
            'shadow-sm ring-1 ring-black/5',
            'min-h-[360px] sm:min-h-[424px] lg:min-h-0',
          ].join(' ')}
        >
          {cover ? (
            <div className="absolute inset-0">
              <Image
                src={cover.src}
                alt={cover.alt}
                fill
                sizes="(max-width: 1024px) 100vw, 66vw"
                className="object-cover opacity-90"
                priority={false}
              />
            </div>
          ) : null}

          <div className="absolute inset-0 bg-gradient-to-br from-black/10 via-black/35 to-black/75" />
          <div className="absolute inset-0 bg-[radial-gradient(900px_circle_at_30%_15%,rgba(255,255,255,0.14),transparent_55%)]" />

          <div className="absolute inset-0 bg-black/35" />
          <div className="absolute inset-x-0 bottom-0 h-56 bg-gradient-to-t from-black/70 via-black/35 to-transparent" />

          <div className="absolute left-6 top-6">
            <IconBadge icon={Scissors} variant="dark" />
          </div>

          <div className="absolute inset-x-0 bottom-0 p-8 sm:p-9">
            <div className="mb-3 inline-flex items-center rounded-full bg-muted px-3 py-1 text-[10px] font-semibold tracking-[0.16em] text-lightblack">
              KIEMELT
            </div>

            <div className="text-[30px] font-semibold leading-[1.05] tracking-tight text-white sm:text-[34px]">
              {getPageTitle(page)}
            </div>

            {desc ? (
              <p className="mt-3 max-w-xl text-[13px] leading-relaxed text-charcoal/90">
                {desc}
              </p>
            ) : null}

            <div className="mt-6">
              <span className="inline-flex items-center gap-2 text-[12px] font-medium text-muted">
                <span className="transition-opacity group-hover:opacity-95">Részletek Megtekintése</span>
                <span aria-hidden className="transition-transform group-hover:translate-x-1">
                  →
                </span>
              </span>
            </div>
          </div>

          <div className="pointer-events-none absolute inset-0 opacity-0 transition-opacity duration-300 group-hover:opacity-100">
            <div className="absolute -inset-40 rotate-12 bg-gradient-to-r from-transparent via-white/12 to-transparent" />
          </div>
        </Link>
      </MotionDiv>
    );
  };

  const hasAny = Boolean(featured || rightTop || rightBottom || bottomLeft || bottomMid || hasCtaCard);

  return (
    <section className="relative bg-charcoal">
      <div className="mx-auto max-w-6xl px-4 pb-24 pt-20 sm:px-6 lg:px-8">
        <MotionDiv {...inViewProps(0)}>
          {!!heading && <Heading className="text-center">{heading}</Heading>}
          <div className="mx-auto mt-5 h-[2px] w-20 rounded-full bg-muted/80" />
          {!!sub_heading && (
            <Subheading className="mx-auto mt-6 max-w-2xl text-center text-secondary">
              {sub_heading}
            </Subheading>
          )}
        </MotionDiv>

        {hasAny && (
          <div className="mt-14 space-y-2">
            <div className="grid grid-cols-1 gap-2 lg:grid-cols-3 lg:items-stretch">
              {featured ? (
                <div className="lg:col-span-2 h-full">
                  <FeaturedCard page={featured} delay={0.05} />
                </div>
              ) : null}

              {rightTop || rightBottom ? (
                <div className={(featured ? 'lg:col-span-1' : 'lg:col-span-3') + ' h-full'}>
                  <div className="grid h-full grid-cols-1 gap-2 lg:grid-rows-2">
                    {rightTop ? <WhiteCard page={rightTop} icon={Sparkles} delay={0.08} /> : null}
                    {rightBottom ? <WhiteCard page={rightBottom} icon={Gem} delay={0.11} /> : null}
                  </div>
                </div>
              ) : null}
            </div>

            <div className="grid grid-cols-1 gap-2 lg:grid-cols-3">
              {bottomLeft ? <WhiteCard page={bottomLeft} icon={Scissors} delay={0.14} /> : null}
              {bottomMid ? <WhiteCard page={bottomMid} icon={User} delay={0.17} /> : null}

              {/* ✅ CTA card csak ha van tartalom */}
              {hasCtaCard ? (
                <MotionDiv {...inViewProps(0.2)} whileHover={hoverLift} className="h-full">
                  <div
                    className={[
                      'relative flex min-h-[210px] h-full flex-col items-center justify-center overflow-hidden rounded-xl p-10 text-center',
                      'bg-gradient-to-br from-lightblack via-lightblack to-black',
                      'ring-1 ring-white/10 shadow-sm transition-shadow hover:shadow-md',
                    ].join(' ')}
                  >
                    <div className="pointer-events-none absolute inset-0 bg-[radial-gradient(900px_circle_at_50%_25%,rgba(255,255,255,0.10),transparent_55%)]" />
                    <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-black/25 via-transparent to-transparent" />

                    <div className="relative">
                      <div className="mx-auto inline-flex h-12 w-12 items-center justify-center rounded-xl bg-white/10 ring-1 ring-white/10">
                        <Calendar className="h-5 w-5 text-white" />
                      </div>

                      {ctaTitle ? (
                        <div className="mt-6 text-[22px] font-semibold tracking-tight text-white">
                          {ctaTitle}
                        </div>
                      ) : null}

                      {ctaDesc ? (
                        <p className="mx-auto mt-2 max-w-xs text-[13px] leading-relaxed text-charcoal/80">
                          {ctaDesc}
                        </p>
                      ) : null}

                      {primaryCTA && ctaHref && ctaHref !== '#' && ctaBtnText ? (
                        <div className="mt-7 flex justify-center">
                          <Button
                            as="a"
                            href={ctaHref}
                            target={ctaTarget}
                            rel={ctaRel}
                            variant="primary"
                            className="h-11 rounded-full bg-muted px-8 text-[12px] font-semibold text-lightblack shadow-sm hover:bg-muted/90"
                          >
                            {ctaBtnText}
                          </Button>
                        </div>
                      ) : null}
                    </div>
                  </div>
                </MotionDiv>
              ) : null}
            </div>
          </div>
        )}
      </div>
    </section>
  );
};
